.Movie {
    background-color: #fff;
    width: 40%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin-bottom: 50px;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    padding: 0 20px;
}

.Movie__Column{
    width: 30%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.Movie__Column:last-child {
    padding: 20px 0;
    width: 60%;
}

.Movie h1 {
    font-size: 20px;
    font-weight: 600;
}

.Movie .Movie__Genres {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin-bottom: 20px;
}

.Movie__Genres .Movie__Genre {
    margin-right: 10px;
    color: #b4b5bd;
}

.Movie .Movie__Synopsis {
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    color: #b4b5bd;
    overflow: hidden;
}

.Movie .Movie__Poster {
    max-width: 100%;
    position: relative;
    top: -20px;
}

@media screen and (min-width:320px) and (max-width:667px){
    .Movie {
        width: 100%;
    }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait) {
    .Movie {
        width: 100%;
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .Movie__Poster {
        top: 0;
        left: 0;
        width: 100%;
    }

    .Movie__Colum {
        width: 100% !important;
    }
}


